<template>
    <div>
      <div class="bottom-wrap">
        <div class="bottom border-bottom">
          <div class="city" @click="changeTab('cinemas')"><span>{{this.isChosen}}<i></i></span></div>
          <div class="brands" @click="changeTab('brands')"><span>{{this.brand}}<i></i></span></div>
          <div class="styles" @click="changeTab('styles')"><span>特色<i></i></span></div>
        </div>
        <div class="close-tab">
          <div class="city-content" v-show="tab === 'cinemas'">
            <div class="region-tab">
              <ul class="tab">
                <li @click="changelistContent" :class="{chosen: listContent}" class="item">商区</li>
                <li @click="changelistContent" :class="{chosen: !listContent}" class="item">地铁站</li>
              </ul>
            </div>
            <div class="region-list">
              <div class="region-sidenav">
                <div v-show="listContent" class="district">
                  <div class="item"
                       v-for="item of cityCinemaList"
                       :class="{active: area == item.name}"
                       @click="changeArea(item.name)"
                       :key="item.id">
                    {{item.name}}({{item.count}})
                  </div>
                </div>
                <div v-show="!listContent" class="subway">
                  <div class="item"
                       v-for="item of subwayList"
                       :class="{active: area == item.name}"
                       @click="area = item.name"
                       :key="item.id">
                    {{item.name}}({{item.count}})
                  </div>
                </div>
              </div>
              <div class="region-list-item">
                <div class="item"
                     v-for="item of cinema"
                     @click="isChosen = item.name"
                     :class="{chosen: isChosen == item.name}"
                     :key="item.id">
                  <span class="item-name">{{item.name}}</span>
                  <span class="item-count">{{item.count}}</span>
                </div>
              </div>
            </div>
          </div>
          <div class="brands-content" v-show="tab === 'brands'">
            <div v-for="item of brandsList" @click="brand = item.name" :class="{chosen: brand === item.name}" class="item brand-list" :key="item.id">
              <span class="brand-name">{{item.name}}</span>
              <span class="brand-count">{{item.count}}</span>
            </div>
          </div>
          <div class="styles-content" v-show="tab === 'styles'">
            <div class="styles">
              <div class="title">特色功能</div>
              <div class="list">
                <div class="item" v-for="item of serviceList" @click="service = item.name" :class="{chosen: service === item.name}" :key="item.id">{{item.name}}</div>
              </div>
              <div class="title">特殊厅</div>
              <div class="list">
                <div class="item" v-for="item of hallTypeList" @click="hallType = item.name" :class="{chosen: hallType === item.name}" :key="item.id">{{item.name}}</div>
              </div>
            </div>
            <div class="btn">
              <span class="reset" @click="reset">重置</span>
              <span class="confirm" @click="confirm">确定</span>
            </div>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'tab',
  data () {
    return {
      cityCinemaList: [],
      subwayList: [],
      regionList: [],
      brandsList: [],
      serviceList: [],
      hallTypeList: [],
      area: '全部',
      isChosen: '全城',
      listContent: true,
      tab: '',
      brand: '品牌',
      service: '全部',
      hallType: '全部'
    }
  },
  methods: {
    getCityCinemaList () {
      axios.get('../../static/广州各区_各区电影院_品牌_特色.json')
        .then((res) => {
          this.cityCinemaList = res.data.district.subItems
          this.subwayList = res.data.subway.subItems
          this.regionList = res.data.district.subItems
          this.brandsList = res.data.brand.subItems
          this.serviceList = res.data.service.subItems
          this.hallTypeList = res.data.hallType.subItems
        })
    },
    changeArea (name) {
      this.area = name
    },
    changelistContent (e) {
      if (e.target.innerText === '商区') {
        this.regionList = this.cityCinemaList
        this.listContent = true
      } else {
        this.regionList = this.subwayList
        this.listContent = false
      }
    },
    changeTab (tabName) {
      if (this.tab === tabName) {
        this.tab = ''
        console.log('tlb', this.tab)
      } else {
        this.tab = tabName
      }
    },
    reset () {
      this.service = '全部'
      this.hallType = '全部'
    },
    confirm () {
      this.tab = ''
    }
  },
  created () {
    this.getCityCinemaList()
  },
  computed: {
    cinema () {
      let arr = {}
      this.regionList.map((value) => {
        if (value.name === this.area) {
          arr = value.subItems
        }
      })
      return arr
    }
  }
}
</script>

<style lang="less" scoped>
  @import '../assets/style/scroll-display';
  .bottom-wrap {
    background-color: #ffffff;
    .bottom {
      line-height: 0.9rem;
      display: flex;
      align-items: center;
      justify-content: space-between;
      flex-direction: row;
      >div {
        width: 33.3%;
        line-height: 0.9rem;
        height: .9rem;
        span {
          width: 100%;
          line-height: .4rem;
          display: inline-block;
          text-align: center;
          &:nth-child(1){
            border-right: 1px solid #e6e6e6;
          }
          i {
            margin-right: 0.1rem;
            width: 0;
            height: 0;
            border: 4px solid #b0b0b0;
            border-left-color: transparent;
            border-right-color: transparent;
            border-bottom-color: transparent;
            display: inline-block;
            margin-left: 0.08rem;
            margin-top: 0.1rem;
          }
        }
      }
    }
    .close-tab {
      position: relative;
      background-color: #ffffff;
      z-index: 999;
      .city-content {
        /*background-color: #f5f5f5;*/
        .region-tab {
          /*background-color: #ffffff;*/
          .tab {
            list-style: none;
            padding: 0;
            margin: 0;
            display: flex;
            justify-content: space-between;
            .item {
              width: 50%;
              line-height: .84rem;
              text-align: center;
            }
            .chosen {
              color: #e54847;
              border-bottom: 2px solid #e54847;
            }
          }
        }
        .region-list {
          height: 6rem;
          display: flex;
          justify-content: space-between;
          background-color: #f5f5f5;
          .region-sidenav {
            width: 35%;
            height: 100%;
            .district, .subway {
              height: 6rem;
              overflow: auto;
              .scroll-display();
              .active {
                background-color: #f5f5f5 !important;
                color: #e54847;
              }
              .item {
                height: .88rem;
                width: 100%;
                text-align: center;
                /*padding-left: .2rem;*/
                line-height: .88rem;
                font-size: 14px;
                text-overflow: ellipsis;
                white-space: nowrap;
                overflow: hidden;
                background-color: #ffffff;
              }
            }
          }
          .region-list-item {
            width: 65%;
            overflow: auto;
            .scroll-display();
            .chosen {
              color: #e54847;
              &:before {
                content: "✔";
                display: block;
                position: absolute;
                left: 8px;
                top: 0;
                width: 11.5px;
                height: 8px;
              }
            }
            .item {
              position: relative;
              height: .88rem;
              line-height: .88rem;
              padding-left: .5rem;
              .item-count {
                float: right;
                margin-right: .2rem;
              }
            }
          }
        }
      }
      .brands-content {
        height: 6rem;
        overflow: auto;
        .scroll-display();
        .brand-list {
          position: relative;
          line-height: 44px;
          height: 44px;
          padding: 0 20px 0 40px;
          border-bottom: 1px solid #e5e5e5;
          color: #333;
          .brand-count {
            float: right;
          }
        }
        .chosen {
          color: #e54847;
          &:before {
            content: "✔";
            display: block;
            position: absolute;
            left: 8px;
            top: 0;
            width: 11.5px;
            height: 8px;
          }
        }
      }
      .styles-content {
        .styles {
          border-bottom: 1px solid #e6e6e6;
          .title {
            line-height: .6rem;
            font-size: 16px;
            color: #686868;
            padding-left: .2rem;
          }
          .list {
            padding: .2rem;
            overflow: hidden;
            .item {
              width: 21%;
              height: .6rem;
              float: left;
              overflow: hidden;
              white-space: nowrap;
              text-overflow: ellipsis;
              margin: 0 0 10px 10px;
              line-height: .6rem;
              text-align: center;
              border: 1px solid #cccccc;
              border-radius: .1rem;
              color: #686868;
            }
            .chosen {
              background: rgba(240, 61, 55, .1);
              border: 1px solid #f03d37;
              color: #f03d37;
            }
          }
        }
        .btn {
          display: flex;
          justify-content: space-between;
          padding: .2rem .4rem;
          >span {
            display: inline-block;
            line-height: .8rem;
            width: 2rem;
            text-align: center;
            border-radius: .1rem;
          }
          .reset {
            border: 1px solid #e5e5e5;
            color: #777777;
          }
          .confirm {
            background-color: #f03d37;
            color: #ffffff;
          }
        }
      }
    }
  }
</style>



