<template>
    <div>
      <router-link to="/filmDetail">
        <div class="movie-detail">
          <div class="bg-filter"></div>
          <div class="poster-bg"></div>
          <div class="detail">
            <div class="img-wrap">
              <img src="http://p0.meituan.net/148.208/movie/363e3a7e614d29b2847ff4e62afcd3f42168651.jpg">
            </div>
            <div class="deail-content">
              <div class="detail-name">毒液：致命守护者</div>
              <div class="detail-ename">Venom</div>
              <div class="detail-score"><span>9.3</span>(66.3万人评)</div>
              <div class="detail-type"><span>动作，惊悚，科幻</span><img src="data:image/png;base64,"></div>
              <div class="detail-src">美国/107分钟</div>
              <div class="detail-puhDate">2018-11-09大陆上映</div>
            </div>
            <div class="info">
              <svg class="icon" aria-hidden="true">
                <use xlink:href="#icon-fanhui"></use>
              </svg>
            </div>
          </div>
        </div>
      </router-link>
    </div>
</template>

<script>
export default {
  name: 'detail-box'
}
</script>

<style lang="less" scoped>
  .movie-detail {
    position: relative;
    height: 3.76rem;
    overflow: hidden;
    /*background-color: #25a4bb;*/
    .bg-filter {
      position: absolute;
      width: 100%;
      height: 100%;
      background-color: #333;
    }
    .poster-bg {
      position: absolute;
      width: 100%;
      height: 100%;
      background: url("http://p0.meituan.net/71.100/movie/363e3a7e614d29b2847ff4e62afcd3f42168651.jpg");
      background-size: cover;
      opacity: .55;
      filter: blur(.4rem);
    }
    .detail {
      position: absolute;
      box-sizing: padding-box;
      width: 100%;
      /*height: 100%;*/
      z-index: 2;
      padding: .3rem;
      display: flex;
      justify-content: left;
      align-items: center;
      color: #e4e5e7;
      .img-wrap {
        width: 2.3rem;
        padding-right: .2rem;
        img {
          width: 100%;
        }
      }
      .deail-content {
        >div {
          margin-top: .2rem;
        }
        .detail-name {
          font-size: .45rem;
          font-weight: bold;
          color: #ffffff;
          margin-top: 0;
        }
        .detail-score {
          font-weight: bold;
          span {
            color: #ffcc00;
            font-size: .38rem;
            font-weight: bold;
            margin-right: .1rem;
          }
        }
        .detail-type {
          line-height: .36rem;
          img {
            height: .36rem;
            margin-left: .1rem;
          }
        }
      }
      .info {
        position: absolute;right: 1rem;
        margin-right: 0;
        svg {
          font-size: .4rem;
          transform: rotate(180deg);
        }
      }
    }
  }
  .icon {
    width: 1em; height: 1em;
    vertical-align: -0.15em;
    fill: currentColor;
    overflow: hidden;
  }
</style>



