<template>
    <div>
      <div class="header">
        <div :class="{masking: tab !== ''}">
          <div class="top">
            <router-link to="/city">{{this.city}}<i></i></router-link>
            <div class="sreach">
              <img src="data:image/png;base64,">
              <input type="text" placeholder="搜影院">
            </div>
          </div>
          <div class="bottom-wrap">
            <div class="bottom border-bottom">
              <div class="city" @click="changeTab('cinemas')"><span>{{this.isChosen}}<i></i></span></div>
              <div class="brands" @click="changeTab('brands')"><span>{{this.brand}}<i></i></span></div>
              <div class="styles" @click="changeTab('styles')"><span>特色<i></i></span></div>
            </div>
            <div class="close-tab" v-show="tab !== ''">
              <div class="city-content" v-show="tab === 'cinemas'">
                <div class="region-tab">
                  <ul class="tab">
                    <li @click="changelistContent" :class="{chosen: listContent}" class="item">商区</li>
                    <li @click="changelistContent" :class="{chosen: !listContent}" class="item">地铁站</li>
                  </ul>
                </div>
                <div class="region-list">
                  <div class="region-sidenav">
                    <div v-show="listContent" class="district">
                      <div class="item"
                           v-for="item of cityCinemaList"
                           :class="{active: area == item.name}"
                           @click="changeArea(item.name)"
                           :key="item.id">
                        {{item.name}}({{item.count}})
                      </div>
                    </div>
                    <div v-show="!listContent" class="subway">
                      <div class="item"
                           v-for="item of subwayList"
                           :class="{active: area == item.name}"
                           @click="area = item.name"
                           :key="item.id">
                        {{item.name}}({{item.count}})
                      </div>
                    </div>
                  </div>
                  <div class="region-list-item">
                    <div class="item"
                         v-for="item of cinema"
                         @click="isChosen = item.name"
                         :class="{chosen: isChosen == item.name}"
                         :key="item.id">
                      <span class="item-name">{{item.name}}</span>
                      <span class="item-count">{{item.count}}</span>
                    </div>
                  </div>
                </div>
              </div>
              <div class="brands-content" v-show="tab === 'brands'">
                <div v-for="item of brandsList" @click="brand = item.name" :class="{chosen: brand === item.name}" class="item brand-list" :key="item.id">
                  <span class="brand-name">{{item.name}}</span>
                  <span class="brand-count">{{item.count}}</span>
                </div>
              </div>
              <div class="styles-content" v-show="tab === 'styles'">
                <div class="styles">
                  <div class="title">特色功能</div>
                  <div class="list">
                    <div class="item" v-for="item of serviceList" @click="service = item.name" :class="{chosen: service === item.name}" :key="item.id">{{item.name}}</div>
                  </div>
                  <div class="title">特殊厅</div>
                  <div class="list">
                    <div class="item" v-for="item of hallTypeList" @click="hallType = item.name" :class="{chosen: hallType === item.name}" :key="item.id">{{item.name}}</div>
                  </div>
                </div>
                <div class="btn">
                  <span class="reset" @click="reset">重置</span>
                  <span class="confirm" @click="confirm">确定</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="cinemasList" ref="listWrapper">
        <div>
          <div class="loading-svg" v-show="loading">
            <svg version="1.1" id="Layer_1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" width="24px" height="30px" viewBox="0 0 24 30" style="enable-background:new 0 0 50 50;" xml:space="preserve">
              <rect x="0" y="0" width="4" height="10" fill="#e54847" transform="translate(0 11.3366)">
                <animateTransform attributeType="xml" attributeName="transform" type="translate" values="0 0; 0 20; 0 0" begin="0" dur="0.6s" repeatCount="indefinite"></animateTransform>
              </rect>
              <rect x="10" y="0" width="4" height="10" fill="#e54847" transform="translate(0 15.3301)">
                <animateTransform attributeType="xml" attributeName="transform" type="translate" values="0 0; 0 20; 0 0" begin="0.2s" dur="0.6s" repeatCount="indefinite"></animateTransform>
              </rect>
              <rect x="20" y="0" width="4" height="10" fill="#e54847" transform="translate(0 1.99673)">
                <animateTransform attributeType="xml" attributeName="transform" type="translate" values="0 0; 0 20; 0 0" begin="0.4s" dur="0.6s" repeatCount="indefinite"></animateTransform>
              </rect>
            </svg>
          </div>
            <div v-for="item of cinemaList" class="item" :key="item.id">
              <div class="item-single">
                <div class="cinema-content">
                  <div class="name">{{item.nm}}<span>{{item.sellPrice}}</span></div>
                  <div class="addr"><p>{{item.addr}}</p><span>{{item.distance}}</span></div>
                  <div class="tag">
                    <div class="operate">
                      <span v-show="item.tag.allowRefund">退</span><span v-show="item.tag.endorse">改签</span>
                    </div>
                    <div class="basic">
                      <span>小吃</span><span v-show="item.tag.vipTag">折扣卡</span>
                    </div>
                    <div class="cinema-style">
                      <span v-for="(i, index) of item.tag.hallType" :key="index">{{i}}</span>
                    </div>
                  </div>
                  <div v-show="item.promotion.cardPromotionTag" class="promotion">
                    <img src="data:image/png;base64,">
                    <p>{{item.promotion.cardPromotionTag}}</p>
                  </div>
                </div>
              </div>
            </div>
        </div>
      </div>
    </div>
</template>

<script>
import BScroll from 'better-scroll'
import axios from 'axios'
import { mapState } from 'vuex'
export default {
  name: 'cinema-list',
  data () {
    return {
      cinemaList: [],
      cityCinemaList: [],
      subwayList: [],
      regionList: [],
      brandsList: [],
      serviceList: [],
      hallTypeList: [],
      area: '全部',
      isChosen: '全城',
      listContent: true,
      tab: '',
      brand: '品牌',
      service: '全部',
      hallType: '全部',
      loading: true
    }
  },
  methods: {
    getCinemaList () {
      axios.get('../../../..//static/黄浦区电影院信息.json')
        .then((res) => {
          this.cinemaList = res.data.cinemas
          this.scroll = new BScroll(this.$refs.listWrapper)
          this.loading = false
        })
    },
    getCityCinemaList () {
      axios.get('../../../..//static/广州各区_各区电影院_品牌_特色.json')
        .then((res) => {
          this.cityCinemaList = res.data.district.subItems
          this.subwayList = res.data.subway.subItems
          this.regionList = res.data.district.subItems
          this.brandsList = res.data.brand.subItems
          this.serviceList = res.data.service.subItems
          this.hallTypeList = res.data.hallType.subItems
        })
    },
    changeArea (name) {
      this.area = name
    },
    changelistContent (e) {
      if (e.target.innerText === '商区') {
        this.regionList = this.cityCinemaList
        this.listContent = true
      } else {
        this.regionList = this.subwayList
        this.listContent = false
      }
    },
    changeTab (tabName) {
      if (this.tab === tabName) {
        this.tab = ''
      } else {
        this.tab = tabName
      }
    },
    reset () {
      this.service = '全部'
      this.hallType = '全部'
    },
    confirm () {
      this.tab = ''
    }
  },
  created () {
    this.getCinemaList()
    this.getCityCinemaList()
  },
  computed: {
    ...mapState({
      city: 'city'
    }),
    cinema () {
      let arr = {}
      this.regionList.map((value) => {
        if (value.name === this.area) {
          arr = value.subItems
        }
      })
      return arr
    }
  }
}
</script>

<style lang="less" scoped>
  @import '../../../assets/style/scroll-display';
  .header{
    .top {
      position: absolute;
      top: 1.08rem;
      left: 0;
      right: 0;
      line-height: 0.9rem;
      display: flex;
      align-items: center;
      justify-content: space-between;
      flex-direction: row;
      background-color: #f5f5f5;
      border-bottom: 1px solid #e6e6e6;
      .sreach {
        margin-right: .3rem;
        width: 75%;
        line-height: .56rem;
        height: .56rem;
        border: 1px solid #e6e6e6;
        border-radius: .1rem;
        background: #ffffff;
        img {
          width: .4rem;
          margin-left: .08rem;
        }
        input {
          text-align: center;
          width: 85%;
        }
      }
      a {
        display: inline-block;
        margin-left: 0.3rem;
        color: #474747;
        white-space: nowrap;
        i {
          margin-right: 0.1rem;
          width: 0;
          height: 0;
          border: 4px solid #b0b0b0;
          border-left-color: transparent;
          border-right-color: transparent;
          border-bottom-color: transparent;
          display: inline-block;
          margin-left: 0.08rem;
          margin-top: 0.1rem;
        }
      }
    }
    .bottom-wrap {
      background-color: #ffffff;
      position: absolute;
      left: 0;
      right: 0;
      top: 2rem;
      .bottom {
        line-height: 0.9rem;
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-direction: row;
        >div {
          width: 33.3%;
          line-height: 0.9rem;
          height: .9rem;
          span {
            width: 100%;
            line-height: .4rem;
            display: inline-block;
            text-align: center;
            &:nth-child(1){
              border-right: 1px solid #e6e6e6;
            }
            i {
              margin-right: 0.1rem;
              width: 0;
              height: 0;
              border: 4px solid #b0b0b0;
              border-left-color: transparent;
              border-right-color: transparent;
              border-bottom-color: transparent;
              display: inline-block;
              margin-left: 0.08rem;
              margin-top: 0.1rem;
            }
          }
        }
      }
      .close-tab {
        position: relative;
        background-color: #ffffff;
        z-index: 999;
        /*&::before {*/
          /*content: "";*/
          /*position: fixed;*/
          /*top: 0;*/
          /*width: 100%;*/
          /*height: 97px;*/
          /*background: rgba(0, 0, 0, .5);*/
        /*}*/
        /*&::after {*/
          /*content: "";*/
          /*position: fixed;*/
          /*bottom: 0;*/
          /*width: 100%;*/
          /*height: 97px;*/
          /*background: rgba(0, 0, 0, .5);*/
        /*}*/
        .city-content {
          /*background-color: #f5f5f5;*/
          .region-tab {
            /*background-color: #ffffff;*/
            .tab {
              list-style: none;
              padding: 0;
              margin: 0;
              display: flex;
              justify-content: space-between;
              .item {
                width: 50%;
                line-height: .84rem;
                text-align: center;
              }
              .chosen {
                color: #e54847;
                border-bottom: 2px solid #e54847;
              }
            }
          }
          .region-list {
            height: 6rem;
            display: flex;
            justify-content: space-between;
            background-color: #f5f5f5;
            .region-sidenav {
              width: 35%;
              height: 100%;
              .district, .subway {
                height: 6rem;
                overflow: auto;
                .scroll-display();
                .active {
                   background-color: #f5f5f5 !important;
                   color: #e54847;
                 }
                .item {
                  height: .88rem;
                  width: 100%;
                  text-align: center;
                  /*padding-left: .2rem;*/
                  line-height: .88rem;
                  font-size: 14px;
                  text-overflow: ellipsis;
                  white-space: nowrap;
                  overflow: hidden;
                  background-color: #ffffff;
                }
              }
            }
            .region-list-item {
              width: 65%;
              overflow: auto;
              .scroll-display();
              .chosen {
                color: #e54847;
                &:before {
                  content: "✔";
                  display: block;
                  position: absolute;
                  left: 8px;
                  top: 0;
                  width: 11.5px;
                  height: 8px;
                }
              }
              .item {
                position: relative;
                height: .88rem;
                line-height: .88rem;
                padding-left: .5rem;
                .item-count {
                  float: right;
                  margin-right: .2rem;
                }
              }
            }
          }
        }
        .brands-content {
          height: 6rem;
          overflow: auto;
          .scroll-display();
          .brand-list {
            position: relative;
            line-height: 44px;
            height: 44px;
            padding: 0 20px 0 40px;
            border-bottom: 1px solid #e5e5e5;
            color: #333;
            .brand-count {
              float: right;
            }
          }
          .chosen {
            color: #e54847;
            &:before {
              content: "✔";
              display: block;
              position: absolute;
              left: 8px;
              top: 0;
              width: 11.5px;
              height: 8px;
            }
          }
        }
        .styles-content {
          .styles {
            border-bottom: 1px solid #e6e6e6;
            .title {
              line-height: .6rem;
              font-size: 16px;
              color: #686868;
              padding-left: .2rem;
            }
            .list {
              padding: .2rem;
              overflow: hidden;
              .item {
                width: 21%;
                height: .6rem;
                float: left;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
                margin: 0 0 10px 10px;
                line-height: .6rem;
                text-align: center;
                border: 1px solid #cccccc;
                border-radius: .1rem;
                color: #686868;
              }
              .chosen {
                background: rgba(240, 61, 55, .1);
                border: 1px solid #f03d37;
                color: #f03d37;
              }
            }
          }
          .btn {
            display: flex;
            justify-content: space-between;
            padding: .2rem .4rem;
            >span {
              display: inline-block;
              line-height: .8rem;
              width: 2rem;
              text-align: center;
              border-radius: .1rem;
            }
            .reset {
              border: 1px solid #e5e5e5;
              color: #777777;
            }
            .confirm {
              background-color: #f03d37;
              color: #ffffff;
            }
          }
        }
      }
    }
  }
  .cinemasList {
    position: absolute;
    top: 2.88rem;
    left: 0;
    right: 0;
    bottom: 1rem;
    overflow: hidden;
    /*overflow-y: auto;*/
    .item {
      padding: .3rem .3rem 0 .3rem;
      .item-single {
        display: flex;
        justify-content: center;
        padding-bottom: .4rem;
        border-bottom: 1px solid #e2e2e2;
        .cinema-content {
          width: 100%;
          .name {
            font-size: .36rem;
            font-weight: bold;
            margin-bottom: .26rem;
            span {
              margin-left: .1rem;
              color: #f0413b;
              font-size: .4rem;
              &::after {
                content: "元起";
                box-sizing: border-box;
                font-size: .24rem;
                margin-left: .1rem;
              }
            }
          }
          .addr {
            color: #555554;
            font-size: .3rem;
            margin-bottom: .1rem;
            width: 100%;
            p {
              width: 85%;
              display: inline-block;
              overflow: hidden;
              white-space: nowrap;
              text-overflow: ellipsis;
            }
            span {
              float: right;
            }
          }
          .tag {
            width: 100%;
            line-height: .4rem;
            display: flex;
            justify-content: left;
            flex-direction: row;
            align-items: center;
            overflow: hidden;
            span {
              padding: .02rem .06rem;
              border-radius: .06rem;
              font-weight: bold;
              font-size: .24rem;
              margin-right: .12rem;
              white-space: nowrap;
            }
            .operate, .cinema-style {
              span {
                color: #589daf;
                border: 1px solid #589daf;
              }
            }
            .basic {
              span {
                color: #f90;
                border: 1px solid #f90;
              }
            }
          }
          .promotion {
            line-height: .4rem;
            margin-top: .26rem;
            img {
              width: .36rem;
              float: left;
              margin-right: .1rem;
            }
            p {
              color: #9a9a9a;
            }
          }
        }
        .distance {
          width: 15%;
          float: right;
          text-align: center;
          position: relative;
          span {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
          }
        }
      }
    }
  }
  .loading-svg {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .masking {
    position: fixed;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    background: rgba(0, 0, 0, .3);
    z-index: 998;
  }
</style>



