<template>
  <div class="nav border-top">
    <router-link to="/movie" class="movie active">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-dianying"></use>
      </svg>
      <span>电影</span>
    </router-link>
    <router-link to="cinema" class="cinema">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-yingyuana"></use>
      </svg>
      <span>影院</span>
    </router-link>
    <router-link to="/my" class="my">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-wode"></use>
      </svg>
      <span>我的</span>
    </router-link>
  </div>
</template>

<script>
export default {
  name: 'buttom-nav'
}
</script>

<style lang="less" scoped>
  .nav {
    line-height: 1rem;
    background-color: #ffffff;
    width: 100%;
    display: flex;
    justify-content: center;
    flex-direction: row;
    /*max-height: 50px;*/
    >a {
      display: inline-block;
      width: 33.3%;
      text-align: center;
      position: relative;
      color: #696969;
      svg {
        font-size: .4rem;
        margin-bottom: .1rem;
      }
      span {
        position: absolute;
        bottom: 0;
        left: 0;
        display: inline-block;
        width: 100%;
        line-height: .4rem;
        font-size: .24rem;
        color: #696969;
      }
    }
    .active {
      color: #e54847;
      span {
        color: #e54847;
      }
    }
    .icon {
      width: 1em; height: 1em;
      vertical-align: -0.15em;
      fill: currentColor;
      overflow: hidden;
    }
  }
</style>



