<template>
    <div>
      <div class="login">
        <div class="login-tab">
          <div @click="changSlider" class="accounts-login">美团帐号登录</div>
          <div @click="changSlider" class="phone-login">手机验证登录</div>
          <div class="slider"></div>
        </div>
        <transition name="accounts" mode="out-in">
          <div v-show="!isPhone" class="accounts-wrap">
            <div class="accounts-bottom-border">
              <div class="accounts">
                <div class="username"><input type="text" placeholder="帐户名/手机号/Email"></div>
                <div class="password"><input type="text" placeholder="请输入你的密码"></div>
              </div>
            </div>
            <div class="btn">
              <button>登录</button>
            </div>
            <div class="subline">
              <ul>
                <li>立即注册</li>
                <li>找回密码</li>
              </ul>
            </div>
            <div class="massage">
              <span>© 猫眼电影 客服电话：</span><a href="#">400-670-5335</a>
            </div>
          </div>
        </transition>
        <transition name="phone" mode="out-in">
          <div v-show="isPhone" class="phone-wrap">
            <div class="phone-bottom-border">
              <div class="phone">
                <div class="username"><input type="text" placeholder="请输入手机号码"><button>发送验证码</button></div>
                <div class="password"><input type="text" placeholder="请输短信验证码"></div>
              </div>
            </div>
            <div class="btn">
              <button>登录</button>
            </div>
            <div class="subline">
              <ul>
                <li>立即注册</li>
                <li>找回密码</li>
              </ul>
            </div>
            <div class="massage">
              <span>© 猫眼电影 客服电话：</span><a href="#">400-670-5335</a>
            </div>
          </div>
        </transition>
      </div>
    </div>
</template>

<script>
export default {
  name: 'Login',
  data () {
    return {
      isPhone: false
    }
  },
  methods: {
    changSlider (e) {
      if (e.target.className === 'accounts-login') {
        document.querySelector('.slider').style.left = '5%'
        this.isPhone = false
      } else {
        document.querySelector('.slider').style.left = '50%'
        this.isPhone = true
      }
    }
  }
}
</script>

<style lang="less" scoped>
  .login {
    position: absolute;
    top: 1.04rem;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
    background-color: #e9e9e9;
    .login-tab {
      overflow: hidden;
      >div {
        position: relative;
        float: left;
        width: 50%;
        line-height: .8rem;
        text-align: center;
        background-color: #ffffff;
      }
    }
    .slider {
      position: absolute;
      left: 5%;
      bottom: 0px;
      width: 45% !important;
      height: 4px;
      background-color: #df2d2d !important;
      transition: left .5s;
    }
    .accounts-wrap {
      position: absolute;
      top: .88rem;
      left: 0;
      right: 0;
      background-color: #e9e9e9;
      .accounts-bottom-border {
        border-bottom: 1px solid #e6e6e6;
        background-color: #ffffff;
        >div {
          margin-left: .2rem;
        }
        .accounts {
          >div {
            line-height: 1rem;
          }
          input {
            width: 95%;
            line-height: .6rem;
            color: #727272;
            padding-left: .1rem;
            &:hover {
              background-color: #e5e5e5;
              border-radius: .06rem;
            }
          }
          .username {
            border-bottom: 1px solid #e6e6e6;
          }
        }
      }
      .btn {
        text-align: center;
        padding: .3rem 0;
        button {
          width: 95%;
          height: .9rem;
          border-radius: .1rem;
          background-color: #e54847;
          font-size: .4rem;
          color: #ffffff;
        }
      }
      .subline {
        ul {
          list-style: none;
          display: flex;
          justify-content: space-between;
          padding: 0 .3rem;
          li {
            color: #e54847;
          }
        }
      }
      .massage {
        margin-top: .2rem;
        text-align: center;
        span {
          margin-right: .1rem;
        }
        a{
          color: #e54847;
        }
      }
    }
    .phone-wrap {
      position: absolute;
      top: .88rem;
      left: 0;
      right: 0;
      background-color: #e9e9e9;
      .phone-bottom-border {
        border-bottom: 1px solid #e6e6e6;
        background-color: #ffffff;
        >div {
          margin-left: .2rem;
        }
        .phone {
          >div {
            line-height: 1rem;
            height: 1rem;
          }
          .username {
            border-bottom: 1px solid #e6e6e6;
            display: flex;
            justify-content: space-between;
            align-items: center;
            flex-direction: row;
            input {
              padding-left: .1rem;
              border-radius: .06rem;
              line-height: .6rem;
              width: 75%;
              &:hover {
                background-color: #ebebe4;
              }
            }
            button {
              padding: .08rem .12rem;
              background-color: #dcdcdc;
              border-radius: .06rem;
              color: #707070;
              margin-right: 2%;
              white-space: nowrap;
            }
          }
          .password {
            input {
              padding-left: .1rem;
              line-height: .6rem;
              width: 95%;
              border-radius: .06rem;
              background-color: #ebebe4;
            }
          }
        }
      }
      .btn {
        text-align: center;
        padding: .3rem 0;
        button {
          width: 95%;
          height: .9rem;
          border-radius: .1rem;
          background-color: #dcdcdc;
          font-size: .4rem;
          color: #ffffff;
        }
      }
      .subline {
        ul {
          list-style: none;
          /*padding: 0;*/
          display: flex;
          justify-content: space-between;
          padding: 0 .3rem;
          li {
            color: #e54847;
          }
        }
      }
      .massage {
        margin-top: .2rem;
        text-align: center;
        span {
          margin-right: .1rem;
        }
        a{
          color: #e54847;
        }
      }
    }
  }
  .accounts-enter-active,
  .accounts-leave-active,
  .phone-enter-active,
  .phone-leave-active {
    transition: all .5s;
  }
  .accounts-enter,
  .accounts-leave-to{
    opacity: 0;
    transform: translateX(-100%);
  }
  .phone-enter,
  .phone-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
    transform: translateX(100%);
  }
</style>



