import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Movie from '@/pages/movie/Movie'
import Cinema from '@/pages/cinema/Cinema'
import My from '@/pages/my/My'
import City from '@/pages/city/City'
import Detail from '@/pages/movie-detail/movie-detail'
import filmDetail from '@/pages/detail/detail'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Movie',
      component: Movie
    },
    {
      path: '/cinema',
      name: 'Cinema',
      component: Cinema
    },
    {
      path: '/my',
      name: 'My',
      component: My
    },
    {
      path: '/city',
      name: 'City',
      component: City
    },
    {
      path: '/detail/:movieId',
      name: 'Detail',
      component: Detail
    },
    {
      path: '/filmDetail',
      name: 'filmDetail',
      component: filmDetail
    }
  ]
})



