<template>
    <div>
      <div :class="{fixed: isFixed}">
        <div class="date-list border-bottom">
          <ul>
            <li @click="changeState" :class="{active: isActive}">今天11月20日</li>
            <li>今天11月20日</li>
            <li>今天11月20日</li>
            <li>今天11月20日</li>
            <li>今天11月20日</li>
            <li>今天11月20日</li>
          </ul>
        </div>
        <tab></tab>
      </div>
      <div class="detail-cinema-list">
        <div class="cinemasList">
          <div class="loading-svg" v-if="loading">
            <svg version="1.1" id="Layer_1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" width="24px" height="30px" viewBox="0 0 24 30" style="enable-background:new 0 0 50 50;" xml:space="preserve">
              <rect x="0" y="0" width="4" height="10" fill="#e54847" transform="translate(0 11.3366)">
                <animateTransform attributeType="xml" attributeName="transform" type="translate" values="0 0; 0 20; 0 0" begin="0" dur="0.6s" repeatCount="indefinite"></animateTransform>
              </rect>
              <rect x="10" y="0" width="4" height="10" fill="#e54847" transform="translate(0 15.3301)">
                <animateTransform attributeType="xml" attributeName="transform" type="translate" values="0 0; 0 20; 0 0" begin="0.2s" dur="0.6s" repeatCount="indefinite"></animateTransform>
              </rect>
              <rect x="20" y="0" width="4" height="10" fill="#e54847" transform="translate(0 1.99673)">
                <animateTransform attributeType="xml" attributeName="transform" type="translate" values="0 0; 0 20; 0 0" begin="0.4s" dur="0.6s" repeatCount="indefinite"></animateTransform>
              </rect>
            </svg>
          </div>
          <div>
            <div v-for="item of cinemaList" class="item" :key="item.id">
              <div class="item-single">
                <div class="cinema-content">
                  <div class="name">{{item.nm}}<span>{{item.sellPrice}}</span></div>
                  <div class="addr"><p>{{item.addr}}</p><span>{{item.distance}}</span></div>
                  <div class="tag">
                    <div class="operate">
                      <span v-show="item.tag.allowRefund">退</span><span v-show="item.tag.endorse">改签</span>
                    </div>
                    <div class="basic">
                      <span>小吃</span><span v-show="item.tag.vipTag">折扣卡</span>
                    </div>
                    <div class="cinema-style">
                      <span v-for="(i, index) of item.tag.hallType" :key="index">{{i}}</span>
                    </div>
                  </div>
                  <div v-show="item.promotion.cardPromotionTag" class="promotion">
                    <img src="data:image/png;base64,">
                    <p>{{item.promotion.cardPromotionTag}}</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
</template>
<script>
import axios from 'axios'
import Tab from '@/components/tab'
export default {
  name: 'cinema-list',
  components: {Tab},
  data () {
    return {
      isActive: true,
      cinemaList: [],
      loading: true,
      isFixed: false
    }
  },
  methods: {
    changeState (e) {
      this.isActive = true
    },
    getCinemaList () {
      axios.get('../../../..//static/黄浦区电影院信息.json')
        .then((res) => {
          this.cinemaList = res.data.cinemas
          this.loading = false
        })
    },
    handleScroll () {
      let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
      if (scrollTop >= 240) {
        this.isFixed = true
      } else {
        this.isFixed = false
      }
    }
  },
  created () {
    window.addEventListener('scroll', this.handleScroll)
    this.getCinemaList()
  }
}
</script>
<style lang="less" scoped>
  @import '../../../assets/style/scroll-display';
  .fixed {
    background-color: #ffffff;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 999;
  }
  .date-list {
    ul {
      padding: 0;
      margin: 0;
      white-space: nowrap;
      overflow: auto;
      .scroll-display();
      border-bottom: 1px solid #f0f0f0;
      li {
        position: relative;
        width: 2rem;
        line-height: .8rem;
        display: inline-block;
        text-align: center;
      }
      .active {
        border-bottom: 2px solid #e54847;
        color: #e54847;
      }
    }
  }
  .detail-cinema-list {
    position: relative;
    .loading-svg {
      position: absolute;
      top: .8rem;
      left: 50%;
      transform: translateX(-50%);
    }
    .item {
      padding: .3rem .3rem 0 .3rem;
      .item-single {
        display: flex;
        justify-content: center;
        padding-bottom: .4rem;
        border-bottom: 1px solid #e2e2e2;
        .cinema-content {
          width: 100%;
          .name {
            font-size: .36rem;
            font-weight: bold;
            margin-bottom: .26rem;
            span {
              margin-left: .1rem;
              color: #f0413b;
              font-size: .4rem;
              &::after {
                content: "元起";
                box-sizing: border-box;
                font-size: .24rem;
                margin-left: .1rem;
              }
            }
          }
          .addr {
            color: #555554;
            font-size: .3rem;
            margin-bottom: .1rem;
            width: 100%;
            p {
              width: 85%;
              display: inline-block;
              overflow: hidden;
              white-space: nowrap;
              text-overflow: ellipsis;
            }
            span {
              float: right;
            }
          }
          .tag {
            width: 100%;
            line-height: .4rem;
            display: flex;
            justify-content: left;
            flex-direction: row;
            align-items: center;
            overflow: hidden;
            span {
              padding: .02rem .06rem;
              border-radius: .06rem;
              font-weight: bold;
              font-size: .24rem;
              margin-right: .12rem;
              white-space: nowrap;
            }
            .operate, .cinema-style {
              span {
                color: #589daf;
                border: 1px solid #589daf;
              }
            }
            .basic {
              span {
                color: #f90;
                border: 1px solid #f90;
              }
            }
          }
          .promotion {
            line-height: .4rem;
            margin-top: .26rem;
            img {
              width: .36rem;
              float: left;
              margin-right: .1rem;
            }
            p {
              color: #9a9a9a;
            }
          }
        }
        .distance {
          width: 15%;
          float: right;
          text-align: center;
          position: relative;
          span {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
          }
        }
      }
    }
  }
</style>



