<template>
  <div class="box">
    <div class="search">
      <input v-model="keyword" type="text" placeholder="输入城市名或拼音">
    </div>
    <div class="search-content" v-show="keyword" ref="search">
      <ul>
        <li class="search-item" v-for="item of list" :key="item.id" @click="handleCityClick(item.name)">{{item.name}}</li>
        <li class="search-item" v-show="hasShow">没有匹配的内容</li>
      </ul>
    </div>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
import { mapMutations } from 'vuex'
export default {
  name: 'CitySearch',
  props: {
    cities: Object
  },
  data () {
    return {
      keyword: '',
      list: [],
      timer: null
    }
  },
  computed: {
    hasShow () {
      return !this.list.length
    }
  },
  mounted () {
    this.scroll = new BScroll(this.$refs.search)
  },
  methods: {
    handleCityClick (city) {
      this.changeCity(city)
      this.$router.push('/movie')
      this.keyword = ''
    },
    ...mapMutations({
      changeCity: 'changeCity'
    })
  },
  watch: {
    keyword () {
      if (this.timer) {
        clearTimeout(this.timer)
      }
      this.timer = setInterval(() => {
        const result = []
        for (let i in this.cities) {
          this.cities[i].forEach((value) => {
            if (value.spell.indexOf(this.keyword) > -1 || value.name.indexOf(this.keyword) > -1) {
              result.push(value)
            }
          })
        }
        this.list = result
      }, 100)
    }
  }
}
</script>

<style lang="less" scoped>
    .search {
      height: .8rem;
      background: #e54847;
      line-height: .8rem;
      text-align: center;
      > input {
        width: 98%;
        height: .6rem;
        border-radius: .04rem;
        text-align: center;
        color: #676767;
        padding: 0 .1rem;
        box-sizing: border-box;
      }
    }
    .search-content {
      position: absolute;
      top: 1.84rem;
      bottom: 0;
      left: 0;
      right: 0;
      background: #e6e6e6;
      z-index: 1;
      overflow: hidden;
      .search-item{
        line-height: .7rem;
        padding-left: .2rem;
        background: #ffffff;
        border-bottom: .02rem solid #eaeaea;
      }
    }
</style>



