<template>
    <div class="header">
      <router-link to="/movie" class="back">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-fanhui"></use>
        </svg>
      </router-link>
      城市选择
    </div>
</template>

<script>
export default {
  name: 'CityHeader'
}
</script>

<style lang="less" scoped>
  .header{
    height: 1.04rem;
    background: #e54847;
    line-height: 1.04rem;
    overflow: hidden;
    color: #fff;
    font-size: .4rem;
    text-align: center;
    position: relative;
    .back{
      position: absolute;
      padding-left: .12rem;
      top: 0;
      left: 0;
      color: #fff;
      >svg{
        font-size: .4rem
      }
    }
  }
  .icon {
    width: 1em; height: 1em;
    vertical-align: -0.15em;
    fill: currentColor;
    overflow: hidden;
  }
</style>



