<template>
  <div>
    <div class="header border-bottom">
      <router-link to="/city">{{this.city}}<i></i></router-link>
      <div class="switch-hot">
        <span :class="{active: active}" @click="changeTable($event)">正在热映</span>
        <span :class="{active: !active}" @click="changeTable($event)">即将上映</span>
      </div>
      <div class="sreach"></div>
    </div>
    <div class="movieLists" v-show="this.table === '正在热映'" ref="listWrapper">
      <div>
        <div class="loading-svg" v-show="loading">
          <svg version="1.1" id="Layer_1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" width="24px" height="30px" viewBox="0 0 24 30" style="enable-background:new 0 0 50 50;" xml:space="preserve">
              <rect x="0" y="0" width="4" height="10" fill="#e54847" transform="translate(0 11.3366)">
                <animateTransform attributeType="xml" attributeName="transform" type="translate" values="0 0; 0 20; 0 0" begin="0" dur="0.6s" repeatCount="indefinite"></animateTransform>
              </rect>
              <rect x="10" y="0" width="4" height="10" fill="#e54847" transform="translate(0 15.3301)">
                <animateTransform attributeType="xml" attributeName="transform" type="translate" values="0 0; 0 20; 0 0" begin="0.2s" dur="0.6s" repeatCount="indefinite"></animateTransform>
              </rect>
              <rect x="20" y="0" width="4" height="10" fill="#e54847" transform="translate(0 1.99673)">
                <animateTransform attributeType="xml" attributeName="transform" type="translate" values="0 0; 0 20; 0 0" begin="0.4s" dur="0.6s" repeatCount="indefinite"></animateTransform>
              </rect>
            </svg>
        </div>
        <div v-for="item of this.moveList" class="list" :key="item.id">
          <router-link :to="{name: 'Detail', params: { movieId: item.id }}">
            <div class="img-wrap">
            <img :src="item.img">
          </div>
            <div class="list-content">
              <div class="list-information">
                <div class="list-title">
                  {{item.nm}}
                  <span v-if="item.version == 'v3d imax'" class="icon"></span>
                </div>
                <div class="list-score">
                  <span>观众评</span>
                  <span class="score">{{item.sc}}</span>
                </div>
                <div class="actors"><span>主演:</span> {{item.star}}</div>
                <div class="show-info">{{item.showInfo}}</div>
              </div>
              <div class="list-btn">
                <button v-if="item.globalReleased" class="g-btn">购票</button>
                <button v-else class="y-btn">预购</button>
              </div>
            </div>
          </router-link>
        </div>
      </div>
    </div>
    <div class="future-movie" v-show="this.table === '即将上映'" ref="Wrapper">
      <div>
        <div class="loading-svg" v-if="loading">
          <svg version="1.1" id="Layer_1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" width="24px" height="30px" viewBox="0 0 24 30" style="enable-background:new 0 0 50 50;" xml:space="preserve">
              <rect x="0" y="0" width="4" height="10" fill="#e54847" transform="translate(0 11.3366)">
                <animateTransform attributeType="xml" attributeName="transform" type="translate" values="0 0; 0 20; 0 0" begin="0" dur="0.6s" repeatCount="indefinite"></animateTransform>
              </rect>
              <rect x="10" y="0" width="4" height="10" fill="#e54847" transform="translate(0 15.3301)">
                <animateTransform attributeType="xml" attributeName="transform" type="translate" values="0 0; 0 20; 0 0" begin="0.2s" dur="0.6s" repeatCount="indefinite"></animateTransform>
              </rect>
              <rect x="20" y="0" width="4" height="10" fill="#e54847" transform="translate(0 1.99673)">
                <animateTransform attributeType="xml" attributeName="transform" type="translate" values="0 0; 0 20; 0 0" begin="0.4s" dur="0.6s" repeatCount="indefinite"></animateTransform>
              </rect>
            </svg>
        </div>
        <div class="expectation">
          <p class="Title">近期最受期待</p>
          <div class="loading-svg" v-if="loading">
            <svg version="1.1" id="Layer_1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" width="24px" height="30px" viewBox="0 0 24 30" style="enable-background:new 0 0 50 50;" xml:space="preserve">
              <rect x="0" y="0" width="4" height="10" fill="#e54847" transform="translate(0 11.3366)">
                <animateTransform attributeType="xml" attributeName="transform" type="translate" values="0 0; 0 20; 0 0" begin="0" dur="0.6s" repeatCount="indefinite"></animateTransform>
              </rect>
              <rect x="10" y="0" width="4" height="10" fill="#e54847" transform="translate(0 15.3301)">
                <animateTransform attributeType="xml" attributeName="transform" type="translate" values="0 0; 0 20; 0 0" begin="0.2s" dur="0.6s" repeatCount="indefinite"></animateTransform>
              </rect>
              <rect x="20" y="0" width="4" height="10" fill="#e54847" transform="translate(0 1.99673)">
                <animateTransform attributeType="xml" attributeName="transform" type="translate" values="0 0; 0 20; 0 0" begin="0.4s" dur="0.6s" repeatCount="indefinite"></animateTransform>
              </rect>
            </svg>
          </div>
          <div class="expectation-list" ref="tab">
            <div class="list">
                <div v-for="item of expectationList" class="list-content" ref="tabitem" :key="item.id">
                  <router-link to="/filmDetail">
                  <div class="img-wrap">
                    <img :src="item.img">
                    <span class="bg-gradient">
                      <span>{{item.wish}}</span>
                      <span>想看</span>
                    </span>
                  </div>
                  <h5 class="title">{{item.nm}}</h5>
                  <p class="data">{{item.comingTitle}}</p>
                  </router-link>
                </div>
            </div>
          </div>
        </div>
        <router-link to="/filmDetail">
          <div v-for="item of comingList" class="comingList" :key="item.id">
            <div class="img-wrap">
              <img :src="item.img">
            </div>
            <div class="list-content">
              <div class="list-information">
                <div class="list-title">
                  {{item.nm}}
                  <span v-if="item.version === 'v3d imax'" class="icon"></span>
                </div>
                <div class="list-score">
                  <span class="score">{{item.wish}}</span>
                  <span>想看</span>
                </div>
                <div class="actors"><span>主演:</span> {{item.star}}</div>
                <div class="show-info">{{item.comingTitle}}</div>
              </div>
              <div class="list-btn">
                <button v-if="item.preShow" class="w-btn">想看</button>
                <button v-else class="y-btn">预购</button>
              </div>
            </div>
          </div>
        </router-link>
      </div>
    </div>
  </div>
</template>
<script>
import axios from 'axios'
import BScroll from 'better-scroll'
import { mapState } from 'vuex'
export default {
  name: 'move-list',
  data () {
    return {
      moveList: [],
      expectationList: [],
      comingList: [],
      table: '正在热映',
      active: true,
      loading: true
    }
  },
  methods: {
    getMovieList () {
      axios.get('../../../..//static/movieList.json')
        .then((res) => {
          this.moveList = res.data.movieList
          this.scroll = new BScroll(this.$refs.listWrapper)
          this.loading = false
        })
    },
    changeTable (e) {
      this.table = e.target.innerText
      if (!e.target.className) {
        this.active = !this.active
        if (e.target.innerText === '即将上映') {
          this.getExpectationList()
          this.loading = !this.loading
        }
      }
    },
    getExpectationList () {
      axios.get('../../../..//static/expectation.json')
        .then((res) => {
          this.expectationList = res.data.coming
          let arr = res.data.coming
          arr.map(function (e) {
            e.comingTitle = e.comingTitle.split(' ')[0]
          })
          this.expectationList = arr
          // this.$nextTick(() => {
          //   this.InitTabScroll()
          // })
        })
      axios.get('../../../..//static/upcoming-movies.json')
        .then((res) => {
          this.comingList = res.data.coming
          this.loading = false
          // this.scroll = new BScroll(this.$refs.Wrapper)
        })
    }
    // InitTabScroll () {
    //   let width = 0
    //   for (let i = 0; i < this.itemList.length; i++) {
    //     width += this.$refs.tabitem[0].getBoundingClientRect().width // getBoundingClientRect() 返回元素的大小及其相对于视口的位置
    //   }
    //   this.$refs.tabWrapper.style.width = width + 'px'
    //   this.$nextTick(() => {
    //     if (!this.scroll) {
    //       this.scroll = new BScroll(this.$refs.tab, {
    //         startX: 0,
    //         click: true,
    //         scrollX: true,
    //         scrollY: false,
    //         eventPassthrough: 'vertical'
    //       })
    //     } else {
    //       this.scroll.refresh()
    //     }
    //   })
    // }
  },
  computed: {
    ...mapState({
      city: 'city'
    })
  },
  mounted () {
    // this.scroll = new BScroll(this.$refs.listWrapper)
  },
  created () {
    this.getMovieList()
  }
}
</script>

<style lang="less" scoped>
  @import '../../../assets/style/scroll-display';
  h5 {
    color: black;
  }
  .header {
    line-height: 0.9rem;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    .sreach {
      width: 0.4rem;
      height: 0.4rem;
      background: url("data:image/png;base64,");
      background-size: 100%;
      margin: 0.21rem;
    }
    a {
      display: inline-block;
      margin-left: 0.3rem;
      color: #464646;
      i {
        margin-right: 0.1rem;
        width: 0;
        height: 0;
        border: 4px solid #b0b0b0;
        border-left-color: transparent;
        border-right-color: transparent;
        border-bottom-color: transparent;
        display: inline-block;
        margin-left: 0.08rem;
        margin-top: 0.1rem;
      }
    }
    .switch-hot {
      span {
        display: inline-block;
        font-family: "Noto Sans S Chinese Medium";
        font-size: 16px;
        padding: 0 .3rem;
      }
      .active {
        border-bottom: 2px solid #e50c00;
        color: #e54847;
      }
    }
  }
  .movieLists {
    position: absolute;
    top: 2rem;
    left: 0;
    right: 0;
    bottom: 1rem;
    overflow: hidden;
    .list {
      padding: 0.2rem;
      overflow: hidden;
      .img-wrap {
        padding-right: 0.2rem;
        width: 1.4rem;
        float: left;
        img {
          width: 100%;
        }
      }
      @media screen and (min-width: 600px) {
        .list-content {
          width: 89% !important;
        }
      }
      .list-content {
        position: relative;
        float: left;
        width: 77%;
        height: 100%;
        padding-bottom: .2rem;
        border-bottom: 1px solid #e6e6e6;
        .list-information {
          width: 90%;
          >div {
            color: #666666;
            margin-top: 0.15rem;
            font-size: 13px;
          }
          .list-title {
            font-family: "Noto Sans S Chinese Medium";
            font-size: 18px;
            color: #000000;
            margin-top: .05rem;
            .icon {
              display: inline-block;
              background: url("data:image/png;base64,");
              background-size: 100%;
              width: .86rem;
              height: .28rem;
              margin-left: .1rem;
            }
          }
          .list-score {
            margin-top: 0.2rem;
            span {
              &:last-child {
                font-size: 16px;
                font-weight: bold;
                color: #fab513;
              }
            }
          }
          .actors {
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
            width: 90%;
          }
        }
        .list-btn {
          position: absolute;
          right: 0;
          top: 0;
          height: 100%;
          line-height: 90px;
          width: 20%;
          text-align: center;
          font-size: .24rem;
          .g-btn {
            background-color: #f03d37;
            padding: .08rem .2rem;
            border-radius: .08rem;
            color: #ffffff;
          }
          .y-btn {
            background-color: #3c9fe6;
            padding: .1rem .2rem;
            border-radius: .08rem;
            color: #ffffff;
          }
        }
      }
    }
  }
  .future-movie {
    position: absolute;
    top: 2rem;
    left: 0;
    right: 0;
    bottom: 1rem;
    overflow: auto;
    .scroll-display();
    /*overflow-y: scroll;*/
    .expectation {
      width: 384px;
      overflow: hidden;
      padding: .3rem;
      border-bottom: .2rem solid #e9e9e9;
      position: relative;
      .Title {
        margin-bottom: .3rem;
        height: 100%;
      }
      .expectation-list{
        height: 3.24rem;
        overflow: hidden;
        .list {
          width: 8rem;
          /*overflow: hidden;*/
          overflow-x: scroll;
          display: flex;
          /*flex-direction: row;*/
          padding-bottom: .2rem;
          .list-content {
            flex: 0 0 84px;
            margin-right: .3rem;
            width: 1.68rem;
            .img-wrap{
              position: relative;
              width: 1.7rem;
              height: 2.3rem;
              img {
                width: 100%;
                height: 100%;
              }
              .bg-gradient {
                display: inline-block;
                position: absolute;
                bottom: 0;
                left: 0;
                width: 100%;
                height: .8rem;
                line-height: 1.3rem;
                background: linear-gradient(transparent, #000000);
                span {
                  color: #faaf00;
                  font-weight: bold;
                  &:first-child{
                    margin-left: .06rem;
                  }
                }
              }
            }
            .title{
              font-weight: bold;
              overflow: hidden;
              margin: .2rem 0;
              width: 100%;
              white-space: nowrap;
              text-overflow: ellipsis;
            }
            .data {
              font-size: .26rem;
              color: #cacaca;
              width: 100%;
              white-space: nowrap;
              overflow: hidden;
              text-overflow: ellipsis;
            }
          }
        }
      }
    }
    .comingList {
      padding: 0.2rem;
      overflow: hidden;
      .img-wrap {
        padding-right: 0.2rem;
        width: 18%;
        float: left;
        img {
          width: 100%;
        }
      }
      .list-content {
        position: relative;
        float: left;
        width: 79%;
        height: 100%;
        padding-bottom: .2rem;
        border-bottom: 1px solid #e6e6e6;
        .list-information {
          width: 90%;
          >div {
            color: #666666;
            margin-top: 0.15rem;
            font-size: 13px;
          }
          .list-title {
            font-family: "Noto Sans S Chinese Medium";
            font-size: 18px;
            color: #000000;
            margin-top: .05rem;
            .icon {
              display: inline-block;
              background: url("data:image/png;base64,");
              background-size: 100%;
              width: .86rem;
              height: .28rem;
              margin-left: .1rem;
            }
          }
          .list-score {
            margin-top: 0.2rem;
            .score {
              font-size: 16px;
              font-weight: bold;
              color: #fab513;
            }
          }
          .actors {
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
            width: 90%;
          }
        }
        .list-btn {
          position: absolute;
          right: 0;
          top: 0;
          height: 100%;
          line-height: 90px;
          width: 1.5rem;
          text-align: center;
          font-size: .24rem;
          .w-btn {
            background-color: #faaf00;
            padding: .08rem .2rem;
            border-radius: .08rem;
            color: #ffffff;
          }
          .y-btn {
            background-color: #3c9fe6;
            padding: .1rem .2rem;
            border-radius: .08rem;
            color: #ffffff;
          }
        }
      }
    }
  }
  .loading-svg {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
</style>



