<template>
    <div class="list" ref="listWrapper">
      <div>
        <div class="current-city">
          <div class="title">当前城市</div>
          <div class="btn-list">
            <div class="btn-wrapper">
              <div class="btn">{{this.currentCity}}</div>
            </div>
          </div>
        </div>
        <div class="hot-city">
          <div class="title">热门城市</div>
          <div class="btn-list">
            <div
              class="btn-wrapper"
              v-for="item of hotCities"
              :key="item.id"
              @click="handleHotClick(item.name)"
            >
              <div class="btn">{{item.name}}</div>
            </div>
          </div>
        </div>
        <div
          class="Initial"
          v-for="(item, key) of cities"
          :key="key"
          :ref="key"
        >
          <div class="title">{{key}}</div>
          <div class="item-list" v-for="city of item" :key="city.id" @click="handleHotClick(city.name)">
            <div class="item">{{city.name}}</div>
          </div>
        </div>
      </div>
    </div>
</template>
<script>
import BScroll from 'better-scroll'
import { mapState, mapMutations } from 'vuex'
export default {
  name: 'CityList',
  data () {
    return {
    }
  },
  props: {
    cities: Object,
    hotCities: Array,
    letter: String
  },
  computed: {
    ...mapState({
      currentCity: 'city'
    })
  },
  mounted () {
    this.scroll = new BScroll(this.$refs.listWrapper)
  },
  methods: {
    handleHotClick (city) {
      // this.$store.commit('changeCity', city)
      this.changeCity(city)
      this.$router.push('/movie')
    },
    ...mapMutations({
      changeCity: 'changeCity'
    })
  },
  watch: {
    letter () {
      if (this.letter) {
        const element = this.$refs[this.letter][0]
        this.scroll.scrollToElement(element)
      }
    }
  }
}
</script>

<style lang="less" scoped>
  .list{
    position: absolute;
    top: 1.84rem;
    bottom: 0;
    left: 0;
    right: 0;
    overflow: hidden;
    .title{
      line-height: .7rem;
      background: #f5f5f5;
      padding-left: .2rem;
      font-size: .24rem;
    }
    .btn-list{
      overflow: hidden;
      padding: .1rem;
      .btn-wrapper{
        width: 33.33%;
        text-align: center;
        float: left;
        .btn{
          text-align: center;
          margin: .1rem;
          padding: .15rem;
          border: .02rem solid #ccc;
          border-radius: .06rem;
        }
      }
    }
    .current-city{
      .btn-list{
        .btn{
          border-color: #e54847;
          color: #e54847;
        }
      }
    }
    .item-list{
      .item{
        line-height: .76rem;
        padding-left: .2rem;
        border-bottom: .02rem solid #ececec;
      }
    }
  }
</style>



