<template>
  <div>
    <div class="header">
      <router-link to="/movie">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-fanhui"></use>
        </svg>
      </router-link>
      {{this.title}}
    </div>
  </div>
</template>

<script>
export default {
  name: 'Header',
  data () {
    return {
      title: '猫眼电影'
    }
  }
}
</script>

<style lang="less" scoped>
  .header {
    position: relative;
    background-color: #e54847;
    line-height: 1.04rem;
    text-align: center;
    color: #ffffff;
    font-size: 18px;
    a {
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      padding: 0 .2rem;
      color: #ffffff;
      line-height: 1.04rem;
      svg {
        font-size: .4rem;
      }
    }
  }
  .icon {
    width: 1em; height: 1em;
    vertical-align: -0.15em;
    fill: currentColor;
    overflow: hidden;
  }
</style>



