<template>
    <div class="scroll-display">
      <Header></Header>
      <detail-box></detail-box>
      <cinema-list></cinema-list>
    </div>
</template>

<script>
import Header from './components/Header'
import DetailBox from './components/detail-box'
import CinemaList from './components/cinema-list'
export default {
  name: 'movie-detail',
  components: {CinemaList, DetailBox, Header}
}
</script>

<style lang="less" scoped>
  @import '../../assets/style/scroll-display';
  .scroll-display {
    .scroll-display()
  }
</style>



