  <div class="header">{{this.title}}</div>
</template>

<script>
export default {
  name: 'Header',
  data () {
    return {
      title: '影院'
    }
  }
}
</script>

<style lang="less" scoped>
  .header {
    background-color: #e54847;
    line-height: 1.04rem;
    text-align: center;
    color: #ffffff;
    font-size: 18px;
  }
</style>



