        width: 96%;
        height: .8rem;
        color: #ffffff;
        border-radius: .1rem;
        background: #e54847;
      }
    }
    .footer {
      margin: 0;
      background-color: transparent;
      .copyright {
        padding: .2rem;
        text-align: center;
        a {
          color: #e54847;
        }
      }
      .register {
        p {
          text-align: center;
          color: #cacaca;
          font-size: 12px;
          padding: .2rem;
        }
      }
    }
  }
  svg {
    transform: rotate(180deg);
  }
  h1 {
    padding: .2rem .2rem;
    font-size: .3rem;
    font-weight: bold;
  }
</style>



